import axios from "axios";
import React, {useEffect, useState} from "react";
import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import Modal from "react-bootstrap/Modal";

import TableFlightBookings from "./FixDept/TableFlightBookings";
import TableFlightBookingsDetails from "./FixDept/TableFlightsData";
import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../utils/Common";
import TableFlightBookingDetails from "../flights/fixed-departure/TableFlightsData";

const FailureLogsHotels = () => {
  const [stats, setStats] = useState([]);
  const [stats2, setStats2] = useState([]);

  /* ----------------------------------------------------------- */

  function showFlightStats() {
    const token = getCookieValue(tokenName);

    axios.get(ipAddress + "admin/fixed-departure-booking-failed", {
      headers: {'Authorization': `Bearer ${token}`,}
    }).then((response) => {
      // console.log(response.data);

      if (response.data.error) {
        errorToast(response.data.message)
      } else {
        // console.log(response.data.recordset);
        setStats(response.data.recordset);
        setStats2(response.data.recordset);
      }

    }).catch(e => {
      errorToast(e.message)
    })
  }

  useEffect(() => {
    showFlightStats();
  }, []);

  /* ----------------------------------------------------------- */

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [flightDetails, setFlightDetails] = useState(null)

  async function ReadFixedDepartureUserDetails(id) {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/fixed-departure-booking-details"

      let res = await axios.post(url, {id}, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(res)
      // console.log(res.data)
      // console.log(res.data.recordset)

      if (res.status === 200 && res.data.error) {
        setFlightDetails([]);
        errorToast(res.data.message)
      } else if (res.status === 200 && !res.data.error) {
        // console.log(res.data.recordset)
        setFlightDetails(res.data.recordset);
        handleShow();
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  return (
   <>
     <CCard>
       <CCardHeader className="card-header-bg">
         <h4 className="mb-0 primary-color">Fix Departure Logs</h4>
       </CCardHeader>

       <CCardBody>
         {stats2 && <TableFlightBookings data={stats2} click={ReadFixedDepartureUserDetails}/>}
       </CCardBody>
     </CCard>

     <Modal show={show} onHide={handleClose} centered fullscreen>
       <Modal.Header closeButton>
         <Modal.Title>Booking Details</Modal.Title>
       </Modal.Header>
       <Modal.Body>
         <TableFlightBookingDetails data={flightDetails}/>
       </Modal.Body>
     </Modal>
   </>
  )
}

export default FailureLogsHotels;
